import styled from "@emotion/styled";

import { theme } from "@hitachivantara/uikit-react-core";

/**
 * The value of the sub header should be coming from UI-KIT, at this time it is not, so we are hardcoding the value
 * here. 64px for the height and an extra 4px for the top padding.
 */
export const SUB_HEADER_HEIGHT = 68;

interface StyledContainerProps {
  showHeaderSubMenu?: boolean;
  isCompactMode?: boolean;
}
interface StyledMainProps {
  paddingTop?: number;
}

const StyledContainer = styled.div<StyledContainerProps>`
  display: flex;
  margin-top: ${props => {
    if (props.showHeaderSubMenu && !props.isCompactMode) {
      return `calc(${theme.header.height} + ${SUB_HEADER_HEIGHT}px)`;
    }

    return theme.header.height;
  }};
`;

const StyledMain = styled.main<StyledMainProps>`
  flex: 1;
  padding-top: ${props => {
    return `${props.paddingTop}px`;
  }};
`;

export { StyledContainer, StyledMain };
